.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Contenido {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px; /* opcional: agregar un espacio entre las columnas */
}

footer {
  background-color: #282c34;
  height: 50px;
  display: flex;
  justify-content: center;
  text-align: center;
  color: white;
  margin: 10px auto 0;
  bottom: 0;
  width: 100%;
  border-top: 2px solid #c03369;
}
